.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 20px 0;
  &__title {
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
  }
  &__wrapper {
    width: 600px;
    box-shadow: 0px 0px 10px #d1d1d1;
    border-radius: 16px;
    padding: 20px;
  }
}